import helper from '../../common/helper.js';
var _self;
export default {
	data() {
		self = this;
		return {
			mobile: '', //手机号
			password: '', //密码
			conpassword: '', //确认密码
			nikename: '', //昵称
			account: '', //平台账号
			plans: '', //选择的平台
			code: "", //输入的验证码
			codes: '',
			codestr: '', //获取的随机数
			choosedaylist: [], //平台选择数组
			// index: 0, //平台初始值
			tipsCode: "获取验证码",
			timeNum: 60,
			clikType: false,
			timeOut: 60,
		};

	},
	onLoad() {
		_self = this;
	},
	methods: {
		openCodeDialog: function() {
			var that = this
			if (that.mobile == '') {
				uni.showToast({
					icon: 'none',
					title: '请输入你的手机号码'
				});
				return;
			} else if (!helper.checkmobile(that.mobile)) {
				uni.showToast({
					icon: 'none',
					title: '请输入正确的手机号码'
				});
				return;
			} else if (!that.clikType) {
				that.getTime();
				that.codestr = helper.MathRand(4);
				that.clikType = true;
				uni.request({
					url: helper.websiteUrl + 'send/sendcode',
					method: 'GET',
					data: {
						loginMark: helper.getloginMark(),
						token: '',
						data: '{"phone": "' + self.mobile + '"}'
					},
					success: res => {
						if (res.data.code == 200) {

							this.codes = res.data.info;
						} else {
							uni.showToast({
								icon: 'none',
								title: '获取验证码失败,请重试'
							});
						}
					},
					fail: () => {
						uni.hideLoading();
						uni.showToast({
							icon: 'none',
							title: '网络异常,请稍后重试'
						});
					}
				});
			}
		},
		// 验证码倒计时
		getTime() {
			var that = this
			var timer = setInterval(function() {
				that.timeNum--;
				if (that.timeNum <= 0) {
					that.tipsCode = "重新获取验证码";
					that.timeNum = that.timeOut;
					that.clikType = false;
					clearInterval(timer);
				} else {
					that.tipsCode = "重新发送" + that.timeNum + "秒";
				}
			}, 1000)
		},

		formSubmit: function(e) {
			var Plans = e.detail.value.plans
			if (this.mobile == '') {
				uni.showToast({
					icon: 'none',
					title: '请输入你的手机号码'
				});
				return;
			}
			if (!helper.checkmobile(this.mobile)) {
				uni.showToast({
					icon: 'none',
					title: '请输入正确的手机号码'
				});
				return;
			}
			if (this.code == "" || this.code != this.codes || this.codes !=
				this.code) {
				uni.showToast({
					icon: 'none',
					title: '验证码错误'
				});
				return;
			}
			if (this.password.length < 6 || this.password.length > 12) {
				uni.showToast({
					icon: 'none',
					title: '请输入6-12个字符的密码'
				});
				return;
			}
			if (this.password != this.conpassword) {
				uni.showToast({
					icon: 'none',
					title: '密码确认错误'
				});
				return;
			}
			// 				if (this.account == '') {
			// 					uni.showToast({
			// 						icon: 'none',
			// 						title: '请输入平台账号'
			// 					});
			// 					return;
			// 				}
			uni.showLoading({
				mask: true,
				title: '注册中，请稍候'
			});
			uni.request({
				url: helper.websiteUrl + 'user/reg',
				data: {
					loginMark: helper.getloginMark(),
					token: '',
					data: '{"mobile": "' + self.mobile + '","password": "' + self.password + '","plans":"' + e.detail.value.plans +
						'"}'
				},
				method: 'GET',
				dataType: 'json',
				success: (res) => {
					uni.hideLoading();
					if (res.data.code == 200) {
						uni.showToast({
							icon: 'none',
							title: '注册成功，请登录',
							duration: 2500,
						});
						uni.removeStorageSync('sdlstate1');
						setTimeout(function() {
							uni.redirectTo({
								url: 'login'
							});
						}, 2800)

					} else {
						//console.log(res.data.info);
						uni.showToast({
							icon: 'none',
							title: res.data.info,
							duration: 2000,
						});
					}
				},
				fail: () => {
					uni.hideLoading();
					uni.showToast({
						icon: 'none',
						title: '网络异常,请稍后重试',
						duration: 2000,
					});
				}
			})
		},
		openlogin() { //打开登录页面 
			uni.navigateTo({
				url: 'login'
			});
		},

	},
}
